<template>
    <div class="center-tab">
        <el-tabs type="border-card">
            <el-tab-pane>
                <span slot="label"><i class="el-icon-date"></i> </span>
                <fullText></fullText>
            </el-tab-pane>
            <el-tab-pane label="" :disabled="!isRule"></el-tab-pane>
            <el-tab-pane label=""></el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
    import fullText from './fullText.vue';
    export default {
        components: { fullText },
        data(){
            return {
                mymsg: '~',
                isRule: false,
            };
        },
        methods: {
            getMessage(val){
                alert(val);
            },
        },
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
    .center-tab{
        padding: 2px; background-color: #b6f7c7;
    }
</style>


