<template>
    <div class="ueditor-app">
        <script ref="script" name="myueditor" type="text/plain"></script>
        <el-row>
            <el-button type="primary" @click="getEditorHtml"></el-button>
        </el-row>
        <p>getContent:</p>
        <div v-html="ueditorCont"></div>
        <p>getAllHtml:</p>
        <div v-html="ueditorHtml"></div>
    </div>
</template>

<script>
    // import './static/UE/ueditor.config.js'
    // import 'static/UE/ueditor.all.min.js'

    export default {
        data(){
            return {
                initValue: '',
                defaultConfig: {
                    // VUE CLI 3  process.env.BASE_URL  VUE CLI 2  UEDITOR_HOME_URL Vue 
                    UEDITOR_HOME_URL: process.env.BASE_URL ? process.env.BASE_URL + 'UEditor/' : '/static/UEditor/',
                    enableAutoSave: false
                },
                ueditorCont: '',
                ueditorHtml: '',
            };
        },
        props: {},
        computed: {},
        created(){},
        mounted(){
            this.initUEditor();
        },
        methods:{
            // 
            initUEditor(){
                this.$refs.script.id = this.id = 'editor_' + Math.random().toString(16).slice(-6); //  Vue SSR id  data  id  BUG
                this.editor = UE.getEditor(this.id, {});
            },
            // 
            getEditorHtml(){
                let self = this;
                self.ueditorCont = self.editor.getContent()
                self.ueditorHtml = self.editor.getAllHtml()
            },
        },
    };
</script>

<style lang="less" scoped>
    .ueditor-app{
        padding: 10px;
    }
</style>


