<template>
    <div class="left-nav">
        <el-input placeholder="" v-model="filterText"></el-input>
        <el-tree class="filter-tree" :data="data" :props="defaultProps" @node-click="nodeClick" default-expand-all :filter-node-method="filterNode" ref="tree"></el-tree>
    </div>
</template>

<script>
    export default {
        components: {},
        data(){
            return {
                filterText: '',
                data: [{
                    id: 100000,
                    label: '',
                    children: [{
                        id: 110000,
                        label: '1 ',
                        children: [{
                            id: 110001,
                            label: '1.1 '
                        }, {
                            id: 110002,
                            label: '1.2 '
                        },{
                            id: 110003,
                            label: '1.3 BCD'
                        }, {
                            id: 110004,
                            label: '1.4 '
                        },{
                            id: 110005,
                            label: '1.5 8051'
                        }, {
                            id: 110006,
                            label: '1.6 8051'
                        }]
                    }, {
                        id: 120000,
                        label: '2 MCS51',
                        children: [{
                            id: 120001,
                            label: '2.1 '
                        }, {
                            id: 120002,
                            label: '2.2 '
                        },{
                            id: 120003,
                            label: '2.3 '
                        }, {
                            id: 120004,
                            label: '2.4 '
                        },{
                            id: 120005,
                            label: '2.5 '
                        }]
                    }, {
                        id: 130000,
                        label: '3 ',
                        children: [{
                            id: 130001,
                            label: '3.1 '
                        }, {
                            id: 130002,
                            label: '3.2 '
                        },{
                            id: 130003,
                            label: '3.3 '
                        }, {
                            id: 130004,
                            label: '3.4 '
                        }]
                    }]
                }],
                defaultProps: {
                    children: 'children',
                    label: 'label'
                }
            };
        },
        watch: {
            filterText(val) {
                this.$refs.tree.filter(val);
            }
        },
        methods: {
            filterNode(value, data) {
                if (!value) return true;
                return data.label.indexOf(value) !== -1;
            },
            // 
            nodeClick(data, node, treeNode){
                let self = this;
                let id = data.id;
                let hasChild = data.children&&data.children.length ? true : false;
                document.getElementById('nav_'+id).scrollIntoView();
            },
        },
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
    .left-nav{
        padding: 2px; background-color: #f7b6ec;
    }
</style>


