                                {label: ''},
                            ],
                        },
                        {
                            label: '',
                        },
                    ]
                }, {
                    label: '',
                    children: [
                        {
                            label: '',
                            children: [
                                {label: ''},
                                {label: ''},
                                {label: ''},
                            ],
                        },
                        {label: ''},
                    ]
                }];
                self.$set(self.summary, 'd12', data);
            },
            // 13()
            getSummary13(){
                let self = this;
                let data = [{
                    label: '',
                    children: [{
                        label: '',
                        children: [
                            {label: ''},
                            {label: ''},
                        ],
                    },{
                        label: '',
                        children: [
                            {label: ''},
                            {label: ''},
                        ],
                    },{
                        label: '',
                        children: [
                            {
                                label: '',
                                children: [
                                    {label: ''},
                                    {label: ''},
                                ]
                            },
                            {label: ''},
                            {label: ''},
                        ],
                    }]
                },{
                    label: '',
                    children: [{
                        label: '',
                        children: [
                            {label: ' '},
                            {label: ''},
                            {label: ''},
                        ],
                    },{
                        label: '',
                        children: [
                            {label: ''},
                            {label: ''},
                        ],
                    },{
                        label: '',
                        children: [
                            {label: ''},
                        ],
                    }]
                }];
                self.$set(self.summary, 'd13', data);
            },
        },
    };
</script>

