                rule3_4: '<p><span style="color: rgb(51, 51, 51); font-family: arial, tahoma, &quot;Microsoft Yahei&quot;, , sans-serif; font-size: 34px; background-color: rgb(255, 255, 255);"></span></p><p><span style="background-color: rgb(255, 255, 255); color: rgb(51, 51, 51); font-family: arial, tahoma, &quot;Microsoft Yahei&quot;, , sans-serif; font-size: 20px;">3.4 </span></p><p><span style="color: rgb(85, 86, 102); font-family: &quot;Microsoft YaHei&quot;, &quot;SF Pro Display&quot;, Roboto, Noto, Arial, &quot;PingFang SC&quot;, sans-serif; font-size: 14px; white-space: pre-wrap; background-color: rgb(255, 255, 255);"></span></p><p><span style="font-size: 12px;"></span></p><p><span style="font-size: 12px;">1. 80C51</span></p><p><br/></p><p><span style="font-size: 12px;">2. 80C51</span></p><p><br/></p><p><span style="font-size: 12px;">3. </span></p><p><br/></p><p><span style="font-size: 12px;">4.  MOVMOVCMOVX</span></p><p><br/></p><p><span style="font-size: 12px;">5. </span></p><p><br/></p><p><span style="font-size: 12px;">6. 80C51RAM128255</span></p><p><br/></p><p><span style="font-size: 12px;">7. 80C51RAM30H=38H, 38H=40H, 40H=48H, 40H=90H</span></p><p><br/></p><p><span style="font-size: 12px;">8. R7R6</span></p><p><br/></p><p><span style="font-size: 12px;">9. 200DH280DH</span></p><p><br/></p><p><span style="font-size: 12px;">10. PC1010H10FFHA.</span></p><p><br/></p><p><span style="font-size: 12px;">11. ABCD1</span></p><p><br/></p><p><span style="font-size: 12px;">12. 20H21H22H23H</span></p><p><br/></p><p><span style="font-size: 12px;">13. </span></p>',
                docContent: '',
                docContentArr: [],
            };
        },
        beforeCreate() { // 
            //   beforeCreate data  methods  
        },
        created() { //
            //   created data  methods 
            //  methods  data  created 
        },
        beforeMount() { //    
            //  beforeMount 
        },
        mounted() { // 
            //  mounted   mounted  
            this.getDoc();
        },
        // 
        beforeUpdate() { //    
            //   beforeUpdate  data  
        },
        updated() {
            // updated  data 
        },
        methods: {
            getDoc(){
                let self = this;

                self.docContentArr = [{
                    id: 100000,
                    content: self.docName,
                    children: [{
                        id: 110000,
                        content: self.chapter1,
                        children: [{
                            id: 110001,
                            content: self.rule1_1
                        }, {
                            id: 110002,
                            content: self.rule1_2
                        },{
                            id: 110003,
                            content: self.rule1_3
                        }, {
                            id: 110004,
                            content: self.rule1_4
                        },{
                            id: 110005,
                            content: self.rule1_5
                        }, {
                            id: 110006,
                            content: self.rule1_6
                        }]
                    }, {
                        id: 120000,
                        content: self.chapter2,
                        children: [{
                            id: 120001,
                            content: self.rule2_1
                        }, {
                            id: 120002,
                            content: self.rule2_2
                        },{
                            id: 120003,
                            content: self.rule2_3
                        }, {
                            id: 120004,
                            content: self.rule2_4
                        },{
                            id: 120005,
                            content: self.rule2_5
                        }]
                    }, {
                        id: 130000,
                        content: self.chapter3,
                        children: [{
                            id: 130001,
                            content: self.rule3_1
                        }, {
                            id: 130002,
                            content: self.rule3_2
                        },{
                            id: 130003,
                            content: self.rule3_3
                        }, {
                            id: 130004,
                            content: self.rule3_4
                        }]
                    }]
                }];
                self.docContent = '';
                self.dealData(self.docContentArr);
            },
            dealData(data){
                let self = this;
                if(!data || !data.length) return false;
                data.forEach(item => {
                    self.docContent += `<div id="nav_${item.id}">${item.content}</div>`;
                    if(item.children && item.children.length){
                        self.dealData(item.children);
                    }
                });
            },
        },
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.full-text{
    min-width: 820px; height: calc(100vh - 160px); overflow: auto;
    .doc-content{
        width: 817px; margin: 0 auto;
    }
}
</style>


